import React, { Component } from 'react';
import { toast } from 'react-toastify';
// import PropTypes from 'prop-types';

class Contacts extends Component {
  state = {
    imageSearch: '',
  };
  handleNameChange = event => {
    this.setState({ imageSearch: event.currentTarget.value.toLowerCase() });
  };

  hendleSubmit = event => {
    event.preventDefault();
    console.log(event);

    if (this.state.imageSearch.trim() === '') {
      toast('Введите Ваш запрос');
      return;
    }
    // this.props.onSubmit(this.state.imageSearch);
    this.setState({ imageSearch: '' });
  };

  render() {
    return (
      <header className="Searchbar">
        <form className="SearchForm" onSubmit={this.hendleSubmit}>
          <button type="submit" className="SearchForm-button">
            <span className="SearchForm-button-label">Search</span>
          </button>

          <input
            className="SearchForm-input"
            type="text"
            // autocomplete="off"
            // autofocus
            placeholder="Search images and photos"
            onChange={this.handleNameChange}
          />
        </form>
      </header>
    );
  }
}

Contacts.propTypes = {};
export default Contacts;
